.signup {
    width        : 280px;
    min-width    : 280px;
    background   : #0D3840;
    border-radius: 8px;
    padding      : 25px;
    display      : block;
    position     : absolute;
    top          : 58px;
    right        : 0;

    button {
        width         : 100%;
        border-radius : 4px;
        color         : white;
        border        : none;
        font-size     : 15px;
        padding-top   : 4px;
        padding-bottom: 4px;
        padding       : 4px 40px 4px 40px;
    }

    z-index: 999;
}


.signup::after {
    right              : auto;
    position           : absolute;
    top                : -14px;
    left               : 52px;
    display            : inline-block !important;
    border-right       : 10px solid transparent;
    border-bottom      : 14px solid #fff;
    border-left        : 10px solid transparent;
    content            : '';
    border-bottom-color: #0D3840;
}

.signup .container {
    display        : flex;
    align-items    : center;
    justify-content: center;
    background     : #01191E;
    border-radius  : 4px;
    padding        : 6px 4px;
    margin-bottom  : 16px;

    img.small {
        width       : 18px;
        height      : auto;
        margin-right: 8px;
    }

    img.big {
        width : 42px;
        height: auto;
    }

    p {
        // margin-left: 8px;
        font-size: 17px;
    }
}

.signup .container:hover,
.signup .semi-container .semi-half:hover {
    cursor : pointer;
    opacity: 0.8;
}

.signup .container.text-center {
    text-align     : center;
    justify-content: center;

    div {
        margin-left: 16px;
    }
}

.signup .userinfo {
    display    : flex;
    align-items: center;

    div {
        margin-left: 8px;

        p.username {
            font-size  : 14px;
            font-weight: 600;
        }

        p.usermail {
            font-size: 10px;
        }
    }
}

.signup .semi-container {
    display        : flex;
    align-items    : center;
    justify-content: space-between;
    margin-bottom  : 16px;

    .semi-half {
        width          : 48%;
        display        : flex;
        justify-content: center;
        align-items    : center;
        background     : #01191E;
        border-radius  : 4px;
        padding        : 6px 8px;

        img.small {
            margin-right: 5px;
            width       : 18px;
            height      : auto;
        }

        img.big {
            width : 42px;
            height: auto;
        }

        p {
            margin-left: 8px;
            font-size  : 17px;
        }
    }
}

.hide_input {
    width : 100%;
    height: 100%;

}